import React, {useEffect} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Card, Empty} from 'antd'
import {_getRoomsLists} from "../../../../redux/actions";
import {useParams} from "react-router-dom";
import {first} from "lodash";
import {Link} from 'react-router-dom'
import Utils from "../../../../utils";

const VillaRoomsLists = (props) => {
    let {_getRoomsLists, loading, data, pagination, params} = props
    const {id} = useParams()
    useEffect(() => {
        if (props?.id !== null) {
            _getRoomsLists({
                villaIn: [props?.id]
            })
        } else {
            _getRoomsLists({
                villaIn: [id]
            })
        }
    }, [_getRoomsLists])

    return (
        <Card bordered={false} className="w-full" title={props?.title}>
            <div className="grid grid-cols-12 gap-4">
                {
                    loading ? (
                        [0, 1, 2, 3, 4, 5].map((item, index) => {
                            return (
                                <div
                                    className="w-full col-span-4 rounded-xl shadow-xl min-h-[280px] relative overflow-hidden animate-pulse bg-gray-200"
                                    key={`skeleton-${index}`}>
                                </div>
                            )
                        })
                    ) : (
                        typeof (data) !== "undefined" && Array.isArray(data) && data.length > 0 ?
                            data.map((item, index) => {
                                return (
                                    <div
                                        className="w-full col-span-4 rounded-xl bg-white shadow-xl min-h-[280px] relative overflow-hidden">
                                        {
                                            typeof (item?.images) !== "undefined" && Array.isArray(item?.images) && item?.images.length > 0 && (
                                                <img
                                                    src={[first(item?.images)?.prefix_url, first(item?.images)?.filename].join("/")}
                                                    alt={[first(item?.images)?.prefix_url, first(item?.images)?.filename].join("/")}
                                                    className={'w-full h-full object-cover relative z-[0]'}
                                                />
                                            )
                                        }
                                        <div className="absolute top-2 right-2 ">

                                        </div>
                                        <div
                                            className="absolute bottom-0 left-0 h-20 w-full bg-gradient-to-t from-black p-2 space-y-2">
                                            <Link to={`/room/${item?._id ?? "undefined"}/${item?.name?.toString()?.toLowerCase()?.replace(/ /g,'-')}`}>
                                                <h3 className={'text-white truncate'}>{item?.name ?? item?.roomName ?? item?.room_name ?? "-"}</h3>
                                            </Link>
                                            <div className="flex justify-between">
                                                <p className={'text-white'}>{[['Rp',Utils.numberFormat(item?.price?.regular)].join(" ") ?? 0, 'day'].join("/")}</p>
                                                <p className={'text-white'}>{item?.currency ?? "-"}</p>
                                            </div>
                                        </div>

                                    </div>
                                )
                            }) : (
                                <div className="col-span-12">
                                    <Empty/>
                                </div>
                            )
                    )
                }
            </div>
        </Card>
    )
}

VillaRoomsLists.propTypes = {
    id: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    _getRoomsLists: PropTypes.func.isRequired
}
VillaRoomsLists.defaultProps = {
    id: null,
    title: "Rooms",
    _getRoomsLists: (value = {}) => {
        console.log({value})
    }
}

export default connect(({Room}) => {
    let {collections} = Room
    return {...collections?.lists}
}, {_getRoomsLists})(React.memo(VillaRoomsLists))