import React, {useEffect, useState} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {useLocation, useParams} from "react-router-dom";
import {getVillaSingle} from "../../../../redux/actions";
import PageHeaderAlt from "components/layout-components/PageHeaderAlt";
import LayoutContent from "components/layout-components/LayoutContent";
import {Card, Row, Col, Button, Typography, Avatar, Image, Tabs, Menu} from 'antd'
import Loading from "../../../../components/shared-components/Loading";
import Flex from "../../../../components/shared-components/Flex";
import {Icon} from 'components/util-components/Icon'
import {
    GlobalOutlined,
    MailOutlined,
    HomeOutlined,
    PhoneOutlined
} from '@ant-design/icons';
import Container from "../../../../components/layout-components/container";
import Utils from "../../../../utils";
import MenusView from "./menus-view";


const {SubMenu} = Menu

const VillaSingle = (props) => {
    let {data, loading, message} = props
    let {getVillaSingle} = props
    const {id} = useParams()

    useEffect(() => {
        getVillaSingle(id)
    }, [getVillaSingle])

    const locations = useLocation()

    const [TabsMenu, setTabsMenu] = useState(() => {
        let tab = new URLSearchParams(locations.search).get("tab-menu")
        if (tab) {
            return tab.toString()?.toLowerCase()
        }
        return "info"
    })
    function setUpMenu(value) {
        setTabsMenu(value)
        if (window) {
            const urlSearch = new URLSearchParams(window.location.search)
            if(urlSearch.get('tab-menu')){
                urlSearch.delete('tab-menu')
                urlSearch.append('tab-menu', value)
            }else{
                urlSearch.append('tab-menu', value)
            }
            let query = []
            urlSearch.forEach((value, key) => {
                query.push(`${key}=${value}`)
            })

            let newUrl = [window.origin, locations.pathname].join("")
            window.history.pushState({article: 'read-all'}, 'Read All Article', [newUrl, query.join("&")].join("?"));

        }
    }


    return loading ? <Loading cover={'page'}/> : (
        <>
            <LayoutContent
                className={'w-full'}
                style={{marginTop: '0px'}}>
                <React.Fragment>
                    <PageHeaderAlt background="/img/others/img-12.jpg" cssClass="bg-primary" overlap>
                        <div className="container text-center">
                            <div className="py-5 my-md-5">
                            </div>
                        </div>
                    </PageHeaderAlt>
                    <Container size={'md'} className={'px-4'}>
                        <Card>
                            <Row justify="center">
                                <Col sm={24} md={23}>
                                    <div className="d-lg-flex">
                                        <div className="rounded p-2 bg-white shadow-sm mx-auto"
                                             style={{'marginTop': '-3.5rem', 'maxWidth': `${150 + 16}px`}}>
                                            <Avatar shape="square" size={150} src={
                                                <Image
                                                    width={150} height={150}
                                                    src={[data?.thumbnail?.prefix_url, data?.thumbnail?.filename].join("/")}
                                                />
                                            }/>
                                        </div>
                                        <div className="ml-md-4 w-100">
                                            <Flex alignItems="center" mobileFlex={false} justifyContent={'between'}
                                                  className="mb-3 text-md-left text-center">
                                                <h2 className="mb-0 mt-md-0 mt-2">{data?.name ?? "-"}</h2>
                                                <div className="ml-md-3 mt-3 mt-md-0">
                                                    <Button size="small" type="primary">Like</Button>
                                                    <Button size="small" className="ml-2">Rate</Button>
                                                </div>
                                            </Flex>
                                            <Row gutter="16">
                                                <Col sm={24} md={8}>
                                                    <p className="mt-0 mr-3 text-muted text-md-left text-center">{data?.bio ?? "-"}</p>
                                                </Col>
                                                <Col xs={24} sm={24} md={8}>
                                                    <Row className="mb-2">
                                                        <Col xs={12} sm={12} md={9}>
                                                            <Icon type={MailOutlined}
                                                                  className="text-primary font-size-md"/>
                                                            <span className="text-muted ml-2">Email:</span>
                                                        </Col>
                                                        <Col xs={12} sm={12} md={15}>
                                                            <a href={data?.contact?.email ? ["mailto", data?.contact?.email].join(":") : '#'}>
                                                                <span
                                                                    className="font-weight-semibold">{data?.contact?.email ?? "-"}</span>
                                                            </a>

                                                        </Col>
                                                    </Row>
                                                    <Row>
                                                        <Col xs={12} sm={12} md={9}>
                                                            <Icon type={PhoneOutlined}
                                                                  className="text-primary font-size-md"/>
                                                            <span className="text-muted ml-2">Phone:</span>
                                                        </Col>
                                                        <Col xs={12} sm={12} md={15}>
                                                            <span
                                                                className="font-weight-semibold">{['+62', (`${data?.contact?.phone_number}`)?.replace(/^0\d{1}$/g, '')].join("") ?? "-"}</span>
                                                        </Col>
                                                    </Row>
                                                </Col>
                                                <Col xs={24} sm={24} md={8}>
                                                    <Row className="mb-2 mt-2 mt-md-0 ">
                                                        <Col xs={12} sm={12} md={9}>
                                                            <Icon type={HomeOutlined}
                                                                  className="text-primary font-size-md"/>
                                                            <span className="text-muted ml-2">Address:</span>
                                                        </Col>
                                                        <Col xs={12} sm={12} md={15}>
                                                            <span
                                                                className="font-weight-semibold">{[(`${data?.locations?.address}`).slice(0, 10), '...'].join('') ?? "-"}</span>
                                                        </Col>
                                                    </Row>
                                                    <Row className="mb-2">
                                                        <Col xs={12} sm={12} md={9}>
                                                            <Icon type={GlobalOutlined}
                                                                  className="text-primary font-size-md"/>
                                                            <span className="text-muted ml-2">Website:</span>
                                                        </Col>
                                                        <Col xs={12} sm={12} md={15}>
                                                            <a target={"_blank"} href={data?.website ?? "#"}>
                                                                <span
                                                                    className="font-weight-semibold">{(`${data?.website}`)?.replace(/htt(p|ps):\/\//g, '')}</span>
                                                            </a>

                                                        </Col>
                                                    </Row>
                                                </Col>
                                            </Row>
                                        </div>
                                    </div>
                                </Col>
                            </Row>
                        </Card>


                        <div className="w-full">
                            <Row gutter={16}>
                                <Col xs={{
                                    span: 24,
                                    order: 2,
                                }}
                                     lg={{
                                         span: 6,
                                         order: 1
                                     }}
                                >
                                    <Menu
                                        defaultSelectedKeys={[TabsMenu]}
                                        mode="inline"
                                    >
                                        {[
                                            {
                                                key:"info",
                                                label:"Info"
                                            },
                                            {
                                                key:"rooms",
                                                label:"Rooms"
                                            },
                                            {
                                                key:"discussions",
                                                label:"Discussions"
                                            },
                                            {
                                                key:"feedback",
                                                label:"Feedback"
                                            },
                                            {
                                                key:"photos",
                                                label:"Photos"
                                            },
                                            {
                                                key:"video",
                                                label:"Videos"
                                            }
                                        ].map((item,index)=> {
                                            return (
                                                <Menu.Item key={item?.key} onClick={()=> setUpMenu(item?.key)}>{item?.label}</Menu.Item>
                                            )
                                        })}
                                    </Menu>
                                </Col>
                                <Col xs={{
                                    span: 24,
                                    order: 1,
                                }}
                                     lg={{
                                         span: 18,
                                         order: 2
                                     }}
                                >
                                    <MenusView tab={TabsMenu}/>
                                </Col>
                            </Row>
                        </div>
                    </Container>
                </React.Fragment>
            </LayoutContent>
        </>
    )
}

VillaSingle.propTypes = {
    getVillaSingle: PropTypes.func.isRequired
}
VillaSingle.defaultProps = {}

export default connect(({Villa}) => {
    let {detail} = Villa
    return {...detail}
}, {getVillaSingle})(VillaSingle)
