import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Card, Empty, Image} from 'antd'
const VillaPhotos = props => {
    let { data,loading } = props
    return (
        <Card bordered={false} loading={loading ?? false} title={'Photos'}>
            <div className="flex flex-wrap items-center gap-4">
                {typeof(data?.photos) !== "undefined" && Array.isArray(data?.photos) && data?.photos.length > 0 ? (
                    data?.photos.map((item,index)=> {
                        return (
                            <div className="rounded-xl overflow-hidden h-[150px]" key={`index-photos-${index}`}>
                                <Image
                                    className={'object-cover'}
                                    width={150}
                                    height={150}
                                    src={[item?.prefix_url,item?.filename].join("/")}
                                />
                            </div>
                        )
                    })
                ):(
                    <div className="w-full">
                        <Empty/>
                    </div>
                )}
            </div>
        </Card>
    )

}

VillaPhotos.propTypes = {}
VillaPhotos.defaultProps = {}

export default connect(({Villa})=> {
    let { detail } = Villa
    return { ...detail }
},{})(React.memo(VillaPhotos))