import React, {useState} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Button, Card, Tabs, Typography} from "antd";
import Utils from "../../../../utils";
import {useLocation} from "react-router-dom";

const VillaSingleInfo = (props)=> {
    let {data, loading, message} = props

    const locations = useLocation()

    const [tabs, setTabs] = useState(() => {
        let tab = new URLSearchParams(locations.search).get("tab")
        if (tab) {
            return tab.toString()?.toLowerCase()
        }
        return "photos"
    })
    function setUp(value) {
        if (window) {

            const urlSearch = new URLSearchParams(window.location.search)
            if(urlSearch.get('tab')){
                urlSearch.delete('tab')
                urlSearch.append('tab', value)
            }else{
                urlSearch.append('tab', value)
            }
            let query = []
            urlSearch.forEach((value, key) => {
                query.push(`${key}=${value}`)
            })

            let newUrl = [window.origin, locations.pathname].join("")
            window.history.pushState({article: 'read-all'}, 'Read All Article', [newUrl, query.join("&")].join("?"));

        }
    }

    function onTabs(value) {
        setUp(value)
    }

    return (
        <Card bordered={false}>
            <div className="mb-3">
                <div>
                    <Typography.Title level={3} className={'p-0 m-0'}>Description</Typography.Title>
                    <Typography.Text>the following is a complete description of the villa</Typography.Text>
                    <div className="py-6">
                        <div dangerouslySetInnerHTML={{__html:data?.description ?? ""}}/>
                    </div>
                </div>
            </div>
            <div className="mb-3">
                <div>
                    <Typography.Title level={3} className={'p-0 m-0'}>Facility</Typography.Title>
                    <Typography.Text>The following facilities are available in this
                        villa</Typography.Text>
                    <div className="flex items-center flex-wrap gap-4 py-6">
                        {
                            Utils.ObjToArr(data?.facility).map((item,index)=> (
                                <Button
                                    disabled={!(item?.value)}
                                    ghost={item?.value}
                                    type={item?.value ? "primary" : "default"}
                                    className={'capitalize'} size={'small'}>{item?.label?.replace(/_/g, ' ') ?? "-"}</Button>
                            ))
                        }
                    </div>
                </div>
            </div>
            <Tabs defaultActiveKey={tabs} onChange={onTabs}>
                <Tabs.TabPane tab={'Photos'} key={'photos'}>
                    <span>Photos</span>
                </Tabs.TabPane>
                <Tabs.TabPane tab={'Videos'} key={'video'}>
                    <span>Videos</span>
                </Tabs.TabPane>
            </Tabs>
        </Card>
    )
}

VillaSingleInfo.propTypes = {}
VillaSingleInfo.defaultProps = {}

export default connect(({Villa}) => {
    let {detail} = Villa
    return {...detail}
},{})(VillaSingleInfo)