import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import VillaSingleInfo from "./villa-single-info";
import VillaRoomsLists from "./villa-rooms-lists";
import VillaDiscussions from "./villa-discussions";
import VillaFeedback from "./villa-feedback";
import VillaPhotos from "./villa-photos";
import VillaVideos from "./villa-videos";


const MenusView = (props)=>{
    let { tab } = props
    switch (tab){
        case "info":
            return <VillaSingleInfo/>
        case "rooms":
            return <VillaRoomsLists/>
        case "discussions":
            return <VillaDiscussions/>
        case "feedback":
            return <VillaFeedback/>
        case "photos":
            return <VillaPhotos/>
        case "video":
            return <VillaVideos/>
        default:
            return <VillaSingleInfo/>

    }
}

MenusView.propTypes = {
    tab: PropTypes.string.isRequired
}
MenusView.defaultProps = {
    tab:"info"
}

export default MenusView